.improvement {
  background-image: url('../img/grey-bg.png');
  background-size: cover;
  background-position: 0 200px;
  background-repeat: no-repeat;
  overflow: hidden;
  &__title {
    @include center-column;
    padding-bottom: 60px;
    @include r(767) {
      padding-bottom: 0; }
    h2 {
      font-size: 60px;
      font-weight: bold;
      color: $dark-two;
      margin-bottom: 15px;
      @include r(767) {
        font-size: 35px;
        text-align: center; } }
    p {
      font-size: 20px;
      line-height: 1.25;
      text-align: center;
      color: $dark-two;
      @include r(767) {
        font-size: 16px; } } }
  &__block {
    padding: 180px 150px;
    position: relative;
    @include r(1200) {
      padding: 180px 0; }
    @include r(767) {
      padding: 50px 0; }
    &-1 {
      .improvement__block__img {
        left: -50px;
        top: 30px;
        @include r(1040) {
          left: 0px; } }
      .improvement__block__parallax {
        top: -20px; } }
    &-2 {
      .improvement__block__img {
        left: -80px;
        @include r(767) {
          left: 0; } }
      .improvement__block__parallax {
        left: 0; } }
    &-3 {
      .improvement__block__parallax {
        left: 0; } }
    &-4 {
      .improvement__block__img {
        left: -80px;
        @include r(767) {
          left: 0; } } }
    &__parallax {
      position: absolute;
      top: -120px;
      right: -20px;
      font-size: 250px;
      font-weight: bold;
      color: rgba(19, 28, 46, 0.05);
      @include r(1040) {
        font-size: 170px;
        right: 0; }
      @include r(767) {
        font-size: 100px;
        top: -50px; }
      @include r(500) {
        font-size: 60px;
        top: -30px; } }
    &__text {
      h3 {
        font-size: 56px;
        font-weight: normal;
        color: $dark-two;
        margin-bottom: 30px;
        line-height: 1;
        @include r(1040) {
          font-size: 50px; }
        @include r(767) {
          font-size: 32px;
          text-align: center; } }
      p {
        font-size: 18px;
        font-weight: normal;
        line-height: 1.67;
        color: $brownish-grey-two;
        max-width: 425px;
        @include r(767) {
          font-size: 16px;
          text-align: center;
          position: relative;
          left: 50%;
          transform: translate(-50%); } } }
    &__img {
      position: relative;
      @include r(767) {
        @include center;
        padding-top: 10px; }
      img {
        width: calc(100% + 50px);
        @include r(767) {
          width: 80%; } } } } }
[data-aos=fade-right] {
  transform: translate3d(-300px,0,0);
  @include r(767) {
    transform: translate3d(-100px,0,0); } }
[data-aos=fade-left] {
  transform: translate3d(300px,0,0);
  @include r(767) {
    transform: translate3d(100px,0,0); } }
