.header {
  padding: 10px 0;
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 30px 0;
  transition: all .5s linear 0s;
}

.header.active {
  top: -140px;
  padding: 10px 0;
  background: #ffffff;
