.pop-up-close {
  width: 30px;
  height: 30px;
  position: absolute;
  right: 20px;
  top: 20px;
}

.pop-up-close span:before, .pop-up-close span:after {
  content: '';
  width: 30px;
  height: 2px;
  background: #000000;
  position: absolute;
  top: 13px;
}

.pop-up-close span:before {
  left: 0;
  transform: rotate(45deg);
}

.pop-up-close span:after {
  right: 0;
  transform: rotate(-45deg);
}

