.baner {
  background-image: url('../img/dx-card.png');
  background-size: 1456px 325px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  height: 480px;
  background-color: #ececee;
  overflow: hidden;
  @include r(767) {
    background-size: 956px auto;
    background-position: 35% 90%; }
  &__content {
    width: 1456px;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    @include r(767) {
      left: 0;
      transform: none;
      width: 100%; } }
  &__block {
    width: 260px;
    position: absolute;
    right: 320px;
    top: 90px;
    @include r(767) {
      position: static;
      margin-top: 15px;
      width: 100%;
      @include center-column; }
    strong {
      font-size: 16px;
      font-weight: bold;
      color: $dark-two;
      line-height: 1;
      text-transform: uppercase; }
    &__footer {
      @include r(767) {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding: 0 15px; } }
    p {
      font-size: 50px;
      font-weight: bold;
      color: $dark-two;
      position: relative;
      padding-bottom: 20px;
      margin-bottom: 30px;
      line-height: 1.1;
      @include r(767) {
        padding-bottom: 0px;
        margin-bottom: 20px; }
      &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 2px;
        width: 64px;
        height: 1px;
        opacity: 0.4;
        background-color: $dark-two;
        @include r(767) {
          left: 50%;
          transform: translate(-50%); } }
      span {
        color: #ff0057; } }
    b {
      font-size: 18px;
      font-weight: bold;
      color: $dark-two;
      margin-bottom: 20px;
      display: block;
      @include r(767) {
        margin: 10px 0;
        margin-right: 15px; } }
    &__app {
      display: flex;
      align-items: center;
      a {
        font-size: 14px;
        font-weight: bold;
        color: #ff0057;
        @include center;
        margin-right: 20px;
        i {
          font-size: 38px;
          margin-right: 10px; } } } } }
